export default {
	data() {
		return {
			userIsLogin: false,
			userInfo: {}	
		};
	},
	onShow() {
		var me = this;
		// 用户状态的切换
		// var userInfo = uni.getStorageSync("globalUser");
		// if (userInfo != null && userInfo != "" && userInfo != undefined) {
		// 	me.userIsLogin = true;
		// 	me.userInfo = userInfo;
		// } else {
		// 	me.userIsLogin = false;
		// 	me.userInfo = {};
		// }

		// 使用挂载方法获取用户数据
		var userInfo = me.getGlobalUser("globalUser");
		if (userInfo != null) {
			me.userIsLogin = true;
			me.userInfo = userInfo;
		} else {
			me.userIsLogin = false;
			me.userInfo = {};
		}
	}
}
